import React, { useState } from 'react';
import { 
  Container, 
  Card, 
  TextField, 
  FormGroup, 
  FormControlLabel, 
  Checkbox, 
  Button, 
  Alert,
  Typography
} from '@mui/material';

const providers = [
  {
    name: 'Telstra',
    coverage: 'Excellent',
    price: 'High',
    minPrice: 40,
    features: ['Widest coverage', 'Fast 5G', 'International roaming']
  },
  {
    name: 'Optus',
    coverage: 'Very Good',
    price: 'Medium',
    minPrice: 30,
    features: ['Good coverage', '5G available', 'Tourist plans']
  },
  {
    name: 'Vodafone',
    coverage: 'Good',
    price: 'Low',
    minPrice: 25,
    features: ['City coverage', 'Budget-friendly', 'International calls']
  }
];

function App() {
  const [budget, setBudget] = useState('');
  const [priorities, setPriorities] = useState({
    coverage: false,
    price: false,
    convenience: false
  });
  const [recommendations, setRecommendations] = useState([]);

  const handleSubmit = () => {
    let filtered = [...providers];
    
    if (budget) {
      filtered = filtered.filter(p => p.minPrice <= parseInt(budget));
    }
    
    filtered.sort((a, b) => {
      let score = 0;
      if (priorities.coverage) {
        score += (a.coverage === 'Excellent' ? 3 : a.coverage === 'Very Good' ? 2 : 1) -
                (b.coverage === 'Excellent' ? 3 : b.coverage === 'Very Good' ? 2 : 1);
      }
      if (priorities.price) {
        score += (a.price === 'Low' ? 3 : a.price === 'Medium' ? 2 : 1) -
                (b.price === 'Low' ? 3 : b.price === 'Medium' ? 2 : 1);
      }
      return -score;
    });
    
    setRecommendations(filtered);
  };

  return (
    <Container maxWidth="md" sx={{ py: 4 }}>
      <Card sx={{ p: 3 }}>
        <Typography variant="h4" gutterBottom>
          Sydney Mobile Service Matcher
        </Typography>
        
        <TextField
          fullWidth
          label="Monthly Budget (AUD)"
          type="number"
          value={budget}
          onChange={(e) => setBudget(e.target.value)}
          sx={{ mb: 3 }}
        />

        <FormGroup sx={{ mb: 3 }}>
          <Typography variant="h6" gutterBottom>
            Priorities:
          </Typography>
          {Object.keys(priorities).map((key) => (
            <FormControlLabel
              key={key}
              control={
                <Checkbox
                  checked={priorities[key]}
                  onChange={() => setPriorities({
                    ...priorities,
                    [key]: !priorities[key]
                  })}
                />
              }
              label={key.charAt(0).toUpperCase() + key.slice(1)}
            />
          ))}
        </FormGroup>

        <Button 
          variant="contained" 
          onClick={handleSubmit}
          fullWidth
          sx={{ mb: 3 }}
        >
          Find Best Options
        </Button>

        {recommendations.map((provider, index) => (
          <Alert 
            severity={index === 0 ? "success" : "info"}
            sx={{ mb: 2 }}
            key={provider.name}
          >
            <Typography variant="h6">{provider.name}</Typography>
            <Typography>Coverage: {provider.coverage}</Typography>
            <Typography>Starting from ${provider.minPrice}/month</Typography>
            <ul>
              {provider.features.map((feature, i) => (
                <li key={i}>{feature}</li>
              ))}
            </ul>
          </Alert>
        ))}
      </Card>
    </Container>
  );
}

export default App;